	module.exports = {"data":{"markdownRemark":{"html":"<p>本土のエンジニアリング会社Mr. Yin Zhihaoを逃した記事を書いてください。 「人々はこの世界に住み、人生を過ごし、何千人もの人々を知るようになります。これらの人々の一部が通り過ぎ、あなたは時々あなたに会いますが、あなたは覚えることができます。ハオ氏は後者に属し、私は彼に二度会いましたが、彼は永遠に私と一緒にいました。彼の心の中で、彼は私たちに別れを告げ、世界を離れ、彼が創設し、育った原因を去りましたが、彼は私たちを離れることはありませんでした。</p>\n<p>\"中国はハオ氏の魂の中​​で高い地位を占めているが、中国人はそれほど気にしない。私は彼についてあまり知らないが、中国人はスマートで能力があり、中国は世界にいないと聞いた。他の国では何度もありますが、私は彼が興奮してこう言っていると聞いています：「私たちはすべて中国の国家の一員であり、歴史、文化、感情は結束しています。中国の人々の感情を自然に表現したものであり、挑戦的なものではなく、中国の20世紀の苦難と苦難の中で、登って登った人々は中国の力を理解することができます。それはどういう意味ですか？</p>\n<p>「ハオランキャンプ」は短命ですが、私たちはすべて永遠のものを持っています。最後の日のミーティングで「私はHaoranキャンプに参加しました。私は非常に深く感じています。違いは、結局のところ、コミュニケーションがたくさんあります。問題を包括的に見なければならないとき、レストランの反対側にある島のカントリースタイルのレストランに行くと、車は長い橋を通り、正面を見ます。しかし、私たちが戻ってきたときに、私たちが橋を渡った時、背の高い建物が高くて立っていて、今すぐ気分が悪いと感じていました。包括的なので、理由は見ることです</p>\n<p>問題は包括的です。二番目の感覚は、私たちが最後の夕食を使うためにポルトガル風のレストランに行き、誰もがたくさんのワインを飲んだということでした。私は少し酔っていて静かに去りました。私は翌日、酔っているとは言わなかった。午前中、昨日酔っていて、夜にドアを開けてベッドに上った。私は突然、私は正直ではないと感じました。問題は同じですが、欠点はありません。問題はあなたが正直でなければならないこと、良いことができないこと、他に欠点があることです。この2つの気持ちは、私がHaoranキャンプで学んだ最も貴重な教訓です。私は自分自身と将来の世界について考えるのは無尽蔵です。 「この経験がハオ氏の期待どおりであるかどうかはわかりません。私は、認識論における人々の利益は、彼が他のやり方で得る可能性のある収入の量よりはるかに大きいと考えています。</p>\n<p>\"ホー氏は建設業界に従事しており、この点で評判を得ている。台北にいたとき、車はYuanshan Hotelを通過してYuanshan Bridgeに私を紹介した。それは何年も前にこのような橋を建てることは容易ではなかった。ニューヨーク、ワシントン、パリ、東京など、世界のいくつかの都市に行ってきました。本土の建物は劣らず、20年前に設立されました。奇妙なことは、中山東路の他の建物のカラフルな照明広告と比べると、本土の建物は清潔で厳粛に見え、それは非常に特徴的なことである。 。</p>\n<p>ハオ氏の有名な言葉は、「人々は自尊心と誇りを持って生きなければならない」ということです。私は趙氏についてはあまり知らないが、この限られた理解に基づいて、私は思う彼はそれをした。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0142.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0142.md","frontmatter":{"title":"8月31日 水曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0142.md absPath of file >>> MarkdownRemark"}}

